<template>
  <div v-if="state" v-zinx class="comp refer_flow">
    <div class="wrapper-mask"></div>
    <div class="refer_flow_box">
      <div class="flow_top">
        <div class="flow_top_left">
          {{ $t('UIwithdrawDetail_TIT') }}
        </div>
        <icon-font
          class="share_btn_icon"
          name="icon-Icon_Cloose"
          :size="variables.flow_share_btn_icon_size"
          :dark="variables.flow_share_btn_icon_color"
          @click="hide"
        />
      </div>
      <div class="flow_content">
        <div class="flow_content_text">{{ $t('UIwithdrawDetail_TIT2') }}</div>
        <div class="flow_content_gold">
          {{ currencySymbol }} {{ this.activityInvite?.activity_user_task?.progress_amount }}
        </div>
      </div>
      <div class="flow_box">
        <time-line></time-line>
      </div>
      <div class="flow_btn">
        <v-btn
          :height="variables.flow_btn_height"
          width="100%"
          color="t_primary_dark_color"
          @click="onLine"
          class="btn_radius sacar_btn"
        >
          <div>{{ $t('UIwithdrawDetail_TIT6') }}</div>
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/referGift.modules.scss'
import referProgress from '@/components/ui/refer-activity/refer-progress'
import referTable from '@/components/ui/refer-activity/refer-table'
import timeLine from './time-line.vue'
export default {
  components: {
    referProgress,
    referTable,
    timeLine
  },
  data() {
    return {
      state: false,
      variables
    }
  },
  computed: {
    ...mapState('dialog'),
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['activityInvite']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    }
  },
  methods: {
    /**
     * 打开动画
     * @param {*} money 金额
     * @param {*} cancel 关闭回调
     */
    show() {
      this.state = !this.state
    },
    // 关闭动画
    hide() {
      this.state = false
    },
    onLine() {
      this.$emit('change')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/referGift.modules.scss';
.refer_flow_box {
  width: 100%;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: $refer_flow_bg;
  border-radius: $refer_flow_border_radius;
  .flow_top {
    width: $flow_top_width;
    padding: $flow_top_padding;
    @include setBox(row, space-between, center);
    .flow_top_left {
      margin: $flow_top_left_margin;
      font-weight: $flow_top_left_font_weight;
      font-size: $flow_top_left_font_size;
      color: $flow_top_left_color;
    }
    .share_btn_icon {
      cursor: pointer;
    }
  }
  .flow_content {
    padding: $flow_content_padding;
    @include setBox(column, center, center);
    .flow_content_text {
      margin: $flow_content_text_margin;
      font-weight: $flow_content_text_font_weight;
      font-size: $flow_content_text_font_size;
      color: $flow_content_text_color;
    }
    .flow_content_gold {
      @include setBox(row, center, center);
      margin: $flow_content_gold_margin;
      font-weight: $flow_content_gold_font_weight;
      font-size: $flow_content_gold_font_size;
      color: $flow_content_gold_color;
    }
    .sacar_btn {
      margin: $flow_sacar_btn_margin;
    }
  }
  .flow_box {
    background: $flow_box_bg;
    padding: $flow_box_padding;
  }
  .flow_btn {
    padding: $flow_btnx_padding;
  }
}

// app 端兼容样式
@media (max-width: 1080px) {
  .refer_flow_box {
    width: $refer_flow_width_app;
    position: fixed;
    top: $refer_flow_top_app;
    left: $refer_flow_left_app;
    transform: $refer_flow_transform_app;
  }
}
// pc 端兼容样式
@media (min-width: 1080px) {
  .refer_flow_box {
    width: $refer_flow_width_pc;
    position: fixed;
    top: $refer_flow_top_pc;
    left: $refer_flow_left_pc;
    transform: $refer_flow_transform_pc;
  }
}
</style>
