<template>
  <div v-zinx v-if="state" class="comp refer_messbox">
    <div class="refer_messbox_box">
      <div class="messbox_box">
        <div class="messbox_box_content"></div>
        <div class="messbox_box_view">
          <div class="messbox_box_title">
            {{ $t('UI_Congratulations') }}
          </div>
          <img class="messbox_box_img" :src="getCacheIcon('referGift_turntable_gold')" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/referGift.modules.scss'
import { getCacheIcon } from '@/assets/imgCache.js'
export default {
  components: {},
  data() {
    return {
      state: false,
      variables
    }
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  methods: {
    /**
     * 打开动画
     * @param {*} money 金额
     * @param {*} cancel 关闭回调
     */
    show() {
      this.state = !this.state
    },
    // 关闭动画
    hide() {
      this.state = false
    },
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/referGift.modules.scss';
.refer_messbox {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .messbox_box {
    width: $messbox_box_width;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: $messbox_box_after_left;
      top: $messbox_box_after_top;
      background: $messbox_box_after_bg;
      width: $messbox_box_after_width;
      height: $messbox_box_after_height;
    }
    &::before {
      content: '';
      position: absolute;
      left: $messbox_box_before_left;
      bottom: $messbox_box_before_bottom;
      background: $messbox_box_before_bg;
      width: $messbox_box_before_width;
      height: $messbox_box_before_height;
    }
    .messbox_box_content {
      width: $messbox_box_content_width;
      height: $messbox_box_content_height;
      position: absolute;
      background: $messbox_box_content_bg;
      top: $messbox_box_content_top;
      left: $messbox_box_content_left;
      bottom: $messbox_box_content_bottom;
      right: $messbox_box_content_right;
      content: '';
      opacity: $messbox_box_content_opacity;
      z-index: 1;
    }
    .messbox_box_view {
      position: relative;
      z-index: 2;
      @include setBox(column, center, center);
      padding: $messbox_box_view_padding;
      .messbox_box_title {
        font-weight: $messbox_box_title_font_weight;
        font-size: $messbox_box_title_font_size;
        color: $messbox_box_title_color;
      }
      .messbox_box_img {
        width: $messbox_box_img_width;
        height: $messbox_box_img_height;
      }
    }
  }
}

// app 端兼容样式
@media (max-width: 1080px) {
  .refer_messbox_box {
    width: $refer_messbox_width_app;
    position: absolute;
    top: $refer_messbox_top_app;
    left: $refer_messbox_left_app;
    transform: $refer_messbox_transform_app;
  }
}
// pc 端兼容样式
@media (min-width: 1080px) {
  .refer_messbox_box {
    width: $refer_messbox_width_pc;
    position: absolute;
    top: $refer_messbox_top_pc;
    left: $refer_messbox_left_pc;
    transform: $refer_messbox_transform_pc;
  }
}
</style>
