<template>
  <div class="refer_gift">
    <div class="refer_gift_box">
      <div class="refer_gift_top">
        <span>
          {{
            $t('UI_Receive_Tit', { Currency: currencySymbol, amount: activityInvite?.activity_user_task?.total_amount })
          }}
        </span>
        <icon-font
          class="refer_gift_top_icon"
          name="icon-Icon_question"
          :size="variables.refer_gift_top_icon_size"
          :dark="variables.refer_gift_top_icon_color"
          @click="onRull"
        />
      </div>
      <div class="refer_gift_progress">
        <div class="refer_gift_progress_box"></div>
        <div class="refer_gift_progress_view">
          <div class="refer_gift_progress_top">
            <div class="progress_top_money">
              <img :src="loadIcon('refer_gift_money', false)" alt="" />
              {{ currencySymbol }}
              <span class="progress_top_money_text">
                <CountTo :startVal="startVal" :endVal="endVal" :duration="duration" :decimals="decimals" />
              </span>
            </div>
            <v-btn
              class="refer_sacar_btn"
              :class="!activityInvite?.activity_user_task?.withdraw_state ? '' : 'disabled'"
              @click="onSacarBtn"
            >
              <img class="sacar_btn_box_img" :src="loadIcon('refer_quest_gold', false)" alt="" />
              <div class="sacar_btn_box_text">
                {{ $t('UIWheelWithDraw') }}
              </div>
            </v-btn>
          </div>
          <div class="refer_gift_progress_bottom">
            <referProgress :progress="progressValue" :strokeWidth="10" :showInfo="false"></referProgress>
            <div
              class="refer_gift_progress_bottom_text"
              v-html="$t('UI_Refer_Withdrawal_Text', { x: remainValue })"
            ></div>
          </div>
        </div>
      </div>
      <div class="refer_gift_turntable">
        <movable-turntable
          :isGoldState="isGoldState"
          :plusSizeLeft="plusSizeLeft"
          :plusSizeTop="plusSizeTop"
          :goldAnimationClass="goldAnimationClass"
          v-if="isTurntableShow"
          ref="turntableRef"
          :turntableBg="turntableBg"
          :size="sizeFn"
          @stopLucky="stopLucky"
          :isBottomBg="false"
        ></movable-turntable>
        <refer-messbox ref="referMessbox"></refer-messbox>
      </div>
      <div class="refer_gift_Share">
        <div class="refer_gift_Share_box">
          <div class="refer_gift_Share_text">
            {{ shareLink }}
          </div>
          <v-btn class="refer_gift_Share_btn" @click="onShare">
            <icon-font
              class="share_btn_icon"
              name="icon-fenxiang1"
              :size="variables.share_btn_icon_size"
              :dark="variables.share_btn_icon_color"
            />
          </v-btn>
        </div>
      </div>
      <div class="refer_gift_tips">
        {{ $t('UI_Withdrawals_Tips') }}
      </div>
      <div class="refer_gift_table">
        <btn-tab
          :width="variables.refer_gift_simple_tab_container_btn_tab_width"
          :radius="variables.refer_gift_simple_tab_container_btn_tab_radius"
          :tag-list="tabs"
          :defaultBg="variables.refer_gift_simple_tab_container_bg"
          :type="btnIndex"
          @changeTab="switchFormTab"
        />
        <!-- 表格数据  -->
        <div class="refer_gift_table_container" v-show="btnIndex == 1">
          <refer-table :amount="activityInvite?.activity_user_task?.total_amount"></refer-table>
        </div>
        <div class="refer_gift_table_container" v-show="btnIndex == 2">
          <help-user-table :list="helpUserList"></help-user-table>
        </div>
      </div>
    </div>
    <refer-flow ref="referFlow" @change="onLink"></refer-flow>
    <refer-rull ref="referRull" :content="activityInvite?.activity_info?.desc"></refer-rull>
    <refer-link ref="referLink" :linkUrl="activityInvite?.activity_info?.share_link" :linkData="linkData"></refer-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import movableTurntable from '@/components/ui/refer-activity/movable-turntable'
import referProgress from '@/components/ui/refer-activity/refer-progress'
import referTable from '@/components/ui/refer-activity/refer-table'
import helpUserTable from '@/components/ui/refer-activity/help-user-table'
import variables from '@style/views/referGift.modules.scss'
import btnTab from '@/components/ui/btn-tab/index'
import referFlow from './ui/refer-flow'
import referRull from './ui/refer-rull'
import referLink from './ui/refer-link'
import referMessbox from './ui/refer-messbox'
import { getToFixed } from '@/utils/helper'
import { spreadWay, getInviteUserList } from '@/api/modules/agent.js'
import loadIcon from '@/assets/icons'
import CountTo from 'vue-count-to'
export default {
  name: 'referGift',
  components: {
    movableTurntable,
    referProgress,
    btnTab,
    referTable,
    referFlow,
    referRull,
    referLink,
    helpUserTable,
    referMessbox,
    CountTo
  },
  data() {
    return {
      variables,
      btnIndex: 1,
      linkData: {}, // 分享连接接口数据
      helpUserList: [], // 获取助力当前任务的用户列表
      isTurntableShow: true,
      startVal: 1, // 字体初始化
      duration: 1500, // 字体滚动速度
      progressValue: 0, //进度条完成值
      goldAnimationClass: 'bank_account_btn_text', // 动画移动重点位置
      isGoldState: true, // 是否需要金币动画
      plusSizeTop: 0, // 金币便宜向上下移动位置
      plusSizeLeft: 50 // 金币便宜向左右移动位置
    }
  },
  watch: {
    isApp(e) {
      // 监听isApp 转盘重置
      this.isTurntableShow = false
      this.$refs.turntableRef && this.$refs.turntableRef.destroy()
      setTimeout(() => {
        this.isTurntableShow = true
      }, 100)
    },
    activityInvite: {
      deep: true, //深度监听设置为 true
      handler: function (newV, oldV) {
        const { activity_user_task = {} } = newV
        if (activity_user_task) {
          const { progress_amount = 0, total_amount = 0 } = activity_user_task
          setTimeout(() => {
            this.progressValue = this.getToFixed(parseFloat(progress_amount / total_amount) * 100)
          }, 200)
        }
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['activityInvite']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 剩余金额
    remainValue() {
      let value =
        this.getToFixed(
          parseFloat(this.activityInvite?.activity_user_task?.total_amount) -
            parseFloat(this.activityInvite?.activity_user_task?.progress_amount)
        ) || 0
      return `<span style=\'color: #FFCE5C;font-size: 20px;\'>${value}</span>`
    },
    //分享连接
    shareLink() {
      return this.activityInvite?.activity_info?.share_link
    },
    sizeFn() {
      const { offsetWidth = 0 } = document.body
      return this.isApp ? offsetWidth : 624
    },
    // tab切换
    tabs() {
      return [
        {
          name: this.$t('UIWheelReport'),
          value: 1
        },
        {
          name: this.$t('UIWheelReference'),
          value: 2
        }
      ]
    },
    turntableBg() {
      return [this.variables.movable_turntable_item1, this.variables.movable_turntable_item2]
    },
    endVal() {
      // 金币结束值
      setTimeout(() => {
        this.startVal = Number(this.activityInvite?.activity_user_task?.progress_amount || 0)
      }, this.duration)
      return Number(this.activityInvite?.activity_user_task?.progress_amount || 0)
    },
    decimals() {
      const progress_amount = this.activityInvite?.activity_user_task?.progress_amount.toString()
      return progress_amount?.split('.')?.[1]?.length || 0
    }
  },
  created() {
    // 如果没有任务禁止进入
    if (!this.activityInvite?.activity_user_task) {
      this.$router.push({ name: 'menu' })
    }
  },
  methods: {
    // 转盘回调
    stopLucky(item) {
      const { type = 2 } = item
      if (type == 2) {
        this.plusSizeTop = 0
        this.plusSizeLeft = 30
        this.goldAnimationClass = 'progress_top_money_text'
      }
      if (type == 1) {
        this.plusSizeTop = 10
        this.plusSizeLeft = 50
        this.goldAnimationClass = 'bank_account_btn_text'
        // 是否更新user信息开关
        this.$store.commit('base/SET_IS_REFRESH_USER', true)
        // 赠送的金币
        this.$store.commit('source/SET_INVITE_GIFT_GOLD', item?.value || 0)
        setTimeout(() => {
          this.user.gold = Number(this.user.gold) + Number(item?.value || 0)
          // 更新user信息
          this.$store.commit('base/SET_USER', this.user)
          // 清空赠送金币
          this.$store.commit('source/SET_INVITE_GIFT_GOLD', 0)
        }, 2000)
      }
    },
    // 点击去提现
    onSacarBtn() {
      const { withdraw_state = 0, progress_amount = 0, total_amount = 0 } = this.activityInvite?.activity_user_task

      if (withdraw_state) return
      // 金额满整  才可提现
      if (Number(progress_amount) >= Number(total_amount)) {
        this.$store.dispatch('modal/setDepositWithdrawShow', { tab: 'withdraw', entrance: 2 })
      } else {
        this.$refs.referFlow.show()
      }
    },
    // 切换手机号还是邮箱
    switchFormTab({ item = {} }) {
      const { value = 1 } = item
      this.btnIndex = value
      if (this.btnIndex == 2) {
        this.getActivityLotteryFn()
      }
    },
    getToFixed(str, nub = 2) {
      return getToFixed(str, nub)
    },
    // 复制链接到剪贴板
    onShare() {
      this.onLink()
    },
    // 打开规则弹窗
    onRull() {
      this.$refs.referRull.show()
    },
    // 打开分享连接弹窗
    async onLink() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const { code = 0, data = {} } = await spreadWay(params).catch(e => e)
      if (code == 0) {
        this.linkData = data
        this.$refs.referLink.show()
      }
    },
    // 获取助力当前任务的用户列表
    async getActivityLotteryFn() {
      const task_id = this.activityInvite?.activity_user_task?.task_id
      const params = {
        token: this.user.token,
        language: this.languageType,
        task_id
      }
      const { code = 0, data = [] } = await getInviteUserList(params).catch(e => e)
      if (code == 0) this.helpUserList = data
    },
    loadIcon: key => loadIcon(key, false)
  }
}
</script>
<style lang="scss" scoped>
@import '@style/views/referGift.modules.scss';
.refer_gift {
  position: relative;
  overflow: hidden;
  @include setBox(row, center, flex-start);
  &::after {
    content: '';
    position: absolute;
    top: $refer_gift_after_top;
    left: $refer_gift_after_left;
    height: $refer_gift_after_height;
    width: $refer_gift_after_width;
    z-index: 1;
  }
  .refer_gift_box {
    @include setBox(column, center, center);
    margin: 0 auto;
    z-index: 2;
    .refer_gift_top {
      width: $refer_gift_top_width;
      position: relative;
      z-index: 2;
      span {
        font-weight: $refer_gift_top_font_weight;
        color: $refer_gift_top_font_color;
      }
    }
    .refer_gift_progress {
      width: $refer_gift_progress_width;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: $refer_gift_progress_after_left;
        top: $refer_gift_progress_after_top;
        background: $refer_gift_progress_after_bg;
        width: $refer_gift_progress_after_width;
        height: $refer_gift_progress_after_height;
      }
      &::before {
        content: '';
        position: absolute;
        left: $refer_gift_progress_before_left;
        bottom: $refer_gift_progress_before_bottom;
        background: $refer_gift_progress_before_bg;
        width: $refer_gift_progress_before_width;
        height: $refer_gift_progress_before_height;
      }
      .refer_gift_progress_box {
        width: $refer_gift_progress_box_width;
        height: $refer_gift_progress_box_height;
        position: absolute;
        background: $refer_gift_progress_box_bg;
        top: $refer_gift_progress_box_top;
        left: $refer_gift_progress_box_left;
        bottom: $refer_gift_progress_box_bottom;
        right: $refer_gift_progress_box_right;
        content: '';
        opacity: $refer_gift_progress_box_opacity;
        z-index: 1;
      }
      .refer_gift_progress_view {
        position: relative;
        z-index: 2;
        .refer_gift_progress_top {
          @include setBox(row, space-between, center);
          .progress_top_money {
            color: $refer_gift_progress_top_color;
            flex-shrink: 1;
            flex-grow: 1;
            text-align: center;
            @include setBox(row, flex-start, center);
          }
          .refer_sacar_btn {
            color: $sacar_btn_color;
            &.disabled {
              opacity: $sacar_btn_box_sacar_btn_box_text_disabled_opacity;
            }
          }
        }
        .refer_gift_progress_bottom {
          .refer_gift_progress_bottom_text {
            margin: $refer_gift_progress_bottom_text_margin;
            text-align: $refer_gift_progress_bottom_text_text_align;
          }
        }
      }
    }
    .refer_gift_turntable {
      position: relative;
      z-index: 2;
    }
    .refer_gift_Share {
      width: $refer_gift_Share_width;
      .refer_gift_Share_box {
        width: $refer_gift_Share_box_width;
        background: $refer_gift_Share_box_bg;
        border-radius: $refer_gift_Share_box_border_radius;
        font-weight: $refer_gift_Share_box_font_weight;
        font-size: $refer_gift_Share_box_font-size;
        color: $refer_gift_Share_box_color;
        @include setBox(row, space-between, center);
        .refer_gift_Share_text {
          flex-basis: 1;
          flex-shrink: 1;
          height: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          -o-text-overflow: ellipsis;
        }
        .refer_gift_Share_btn {
          border-radius: $refer_gift_Share_border_radius;
          cursor: pointer;
        }
      }
    }
    .refer_gift_tips {
      margin: $refer_gift_tips_margin;
      font-size: $refer_gift_Share_font_size;
      padding: 0 20px;
    }
    .refer_gift_table {
      width: $refer_gift_table_width;
      padding: $refer_gift_table_padding;
      .refer_gift_table_container {
        height: $refer_gift_table_container_height;
        margin: $refer_gift_table_container_margin;
      }
    }
  }
}
// app 端兼容样式
@media (max-width: 1080px) {
  .refer_gift {
    &::after {
      background: $refer_gift_after_bg_app;
    }
    .refer_gift_box {
      width: $refer_gift_box_width_app;
      .refer_gift_top {
        padding: $refer_gift_top_padding_app;
        @include setBox(row, space-between, center);
        span {
          font-size: $refer_gift_top_span_font_size_app;
        }
      }
      .refer_gift_progress {
        .refer_gift_progress_view {
          padding: $refer_gift_progress_view_padding_app;
          .refer_gift_progress_top {
            padding: $refer_gift_progress_top_padding_app;
            .progress_top_money {
              font-size: $progress_top_money_font_size_app;
              img {
                width: $progress_top_money_img_width_app;
                height: $progress_top_money_img_height_app;
                margin: $progress_top_money_img_margin_app;
              }
            }
            .refer_sacar_btn {
              // width: $sacar_btn_width_app;
              font-size: $sacar_btn_font_size_app;
              border-radius: $sacar_btn_box_border_radius;
              .sacar_btn_box_img {
                width: $sacar_btn_box_sacar_btn_box_img_width_app;
                height: $sacar_btn_box_sacar_btn_box_img_height_app;
              }
            }
          }
          .refer_gift_progress_bottom {
            .refer_gift_progress_bottom_text {
              font-size: $refer_gift_progress_bottom_text_font_size_app;
            }
          }
        }
      }
      .refer_gift_turntable {
        margin: $refer_gift_turntable_margin_app;
      }
      .refer_gift_Share {
        margin: $refer_gift_Share_margin_app;
        padding: $refer_gift_Share_padding_app;
        .refer_gift_Share_box {
          padding: $refer_gift_Share_box_padding_app;
          .refer_gift_Share_btn {
            width: $refer_gift_Share_btn_width_app;
            height: $refer_gift_Share_btn_height_app;
          }
        }
      }
    }
  }
}
// pc 端兼容样式
@media (min-width: 1080px) {
  .refer_gift {
    &::after {
      background: $refer_gift_after_bg_pc;
    }
    .refer_gift_box {
      width: $refer_gift_box_width_pc;
      .refer_gift_top {
        padding: $refer_gift_top_padding_pc;
        @include setBox(row, center, center);
        span {
          font-size: $refer_gift_top_span_font_size_pc;
        }
        .refer_gift_top_icon {
          position: absolute;
          top: $refer_gift_top_icon_top_pc;
          transform: $refer_gift_top_icon_transform_pc;
          right: $refer_gift_top_icon_right_pc;
          cursor: pointer;
        }
      }
      .refer_gift_progress {
        .refer_gift_progress_view {
          padding: $refer_gift_progress_view_padding_pc;
          .refer_gift_progress_top {
            padding: $refer_gift_progress_top_padding_pc;
            .progress_top_money {
              font-size: $progress_top_money_font_size_pc;
              img {
                width: $progress_top_money_img_width_pc;
                height: $progress_top_money_img_height_pc;
                margin: $progress_top_money_img_margin_pc;
              }
            }
            .refer_sacar_btn {
              // width: $sacar_btn_width_pc;
              font-size: $sacar_btn_font_size_pc;
              border-radius: $sacar_btn_box_border_radius;
              .sacar_btn_box_img {
                width: $sacar_btn_box_sacar_btn_box_img_width_pc;
                height: $sacar_btn_box_sacar_btn_box_img_height_pc;
              }
            }
          }
          .refer_gift_progress_bottom {
            .refer_gift_progress_bottom_text {
              font-size: $refer_gift_progress_bottom_text_font_size_pc;
            }
          }
        }
      }
      .refer_gift_turntable {
        margin: $refer_gift_turntable_margin_pc;
        position: relative;
      }
      .refer_gift_Share {
        margin: $refer_gift_Share_margin_pc;
        padding: $refer_gift_Share_padding_app;
        .refer_gift_Share_box {
          padding: $refer_gift_Share_box_padding_pc;
          .refer_gift_Share_btn {
            width: $refer_gift_Share_btn_width_pc;
            height: $refer_gift_Share_btn_height_pc;
          }
        }
      }
    }
  }
}
</style>
