<template>
  <ul class="timeline">
    <li class="timeline_item" v-for="(item, index) in list" :key="index">
      <div class="item_tail" v-if="list.length - 1 != index"></div>
      <div class="item_node"></div>
      <icon-font
        class="item_node_icon"
        name="icon-icon_check_mark"
        :size="variables.item_node_icon_size"
        :dark="variables.item_node_icon_color"
        v-if="list.length - 1 != index"
      />
      <div class="item_wrapper">
        <div class="item_content">{{ item.content }}</div>
      </div>
    </li>
  </ul>
</template>
<script>
import { getToFixed } from '@/utils/helper'
import { mapState } from 'vuex'
import variables from '@style/views/referGift.modules.scss'
export default {
  data() {
    return {
      variables
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['activityInvite']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    remainValue() {
      let value = this.getToFixed(
        parseFloat(this.activityInvite?.activity_user_task?.total_amount) -
          parseFloat(this.activityInvite.activity_user_task.progress_amount)
      )
      return value
    },
    totalAmount() {
      return this.getToFixed(parseFloat(this.activityInvite?.activity_user_task?.total_amount))
    },
    list() {
      return [
        {
          content: this.$t('UIwithdrawDetail_TIT2')
        },
        {
          content: this.$t('UIwithdrawDetail_TIT4', { amount: this.remainValue })
        },
        {
          content: this.$t('UIwithdrawDetail_TIT5', { amount: this.currencySymbol + this.totalAmount })
        }
      ]
    }
  },
  methods: {
    getToFixed(str, nub = 2) {
      return getToFixed(str, nub)
    }
  }
}
</script>
<style scoped lang="scss">
@import '@style/views/referGift.modules.scss';

.timeline {
  margin: $timeline_margin;
  font-size: $timeline_font_size;
  list-style: none;
  padding: $timeline_font_padding;
  .timeline_item {
    position: relative;
    padding: $timeline_timeline_item_padding;
    .item_tail {
      position: absolute;
      left: $item_tail_left;
      height: $item_tail_height;
      border-left: $item_tail_border_left;
      opacity: $item_tail_opacity;
    }
    .item_node {
      left: $item_node_left;
      width: $item_node_width;
      height: $item_node_height;
      position: absolute;
      background: $item_node_bg;
      border-radius: $item_node_border_radius;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .item_node_icon {
      left: $item_node_icon_left;
      top: $item_node_icon_top;
      position: absolute;
      display: flex;
      z-index: 2;
    }
    .item_wrapper {
      position: relative;
      padding-left: $item_wrapper_padding_left;
      top: $item_wrapper_top;
      .item_content {
        font-weight: $item_content_font_weigh;
        font-size: $item_content_font_size;
        color: $item_content_color;
      }
    }
  }
}
</style>
