<template>
  <div v-if="state" v-zinx class="comp refer_rull">
    <div class="wrapper-mask"></div>
    <div class="refer_rull_box">
      <div class="rull_top">
        <div class="rull_top_left"></div>
        <icon-font
          class="share_btn_icon"
          name="icon-Icon_Cloose"
          :size="variables.refer_rull_share_btn_icon_size"
          :dark="variables.refer_rull_share_btn_icon_color"
          @click="hide"
        />
      </div>
      <div class="rull_title">
        {{ $t('UI.Settings_0_main_5') }}
      </div>
      <div class="rull_content" v-html="getContent(content)"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/referGift.modules.scss'
import { getEeplace } from '@/utils/helper.js'
export default {
  props: {
    content: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      state: false,
      variables
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp'])
  },
  watch: {
    // 如果弹窗清了，就要关闭
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    /**
     * 打开动画
     * @param {*} money 金额
     * @param {*} cancel 关闭回调
     */
    show() {
      this.state = !this.state
    },
    // 关闭动画
    hide() {
      this.state = false
      if (this.remove) this.remove()
    },
    getContent(content) {
      return getEeplace(content)
    }
  }
}
</script>

