<template>
  <div v-if="state" v-zinx class="comp refer_link">
    <div class="wrapper-mask"></div>
    <div class="refer_link_box">
      <div class="link_top">
        <div class="link_top_left">
          {{ $t('UIWheelShareTit') }}
        </div>
        <icon-font
          class="share_btn_icon"
          name="icon-Icon_Cloose"
          :size="variables.link_share_btn_icon_size"
          :dark="variables.link_share_btn_icon_color"
          @click="hide"
        />
      </div>
      <div class="link_content">
        <div class="link_content_Share">
          <div class="link_content_Share_text">{{ linkUrl }}</div>
          <v-btn class="link_content_Share_btn" @click="onCopy">
            {{ $t('UI_copy') }}
          </v-btn>
        </div>
        <div class="link_content_code" ref="QRCrodeRef"></div>
        <div class="link_content_text">
          {{ $t('UI_UIPromoteMain_1') }}
        </div>
        <div class="link_btn">
          <v-btn
            height="36"
            width="100%"
            color="t_primary_dark_color"
            @click="downloadIamge"
            class="btn_radius sacar_btn"
          >
            <div>{{ $t('UI_UIPromoteMain_2') }}</div>
          </v-btn>
        </div>
        <div class="link_icon">
          <div class="link_icon_title">{{ $t('UI_Promote_ShareTitle') }}</div>
          <div class="link_icon_btn">
            <icon-font
              class="party cursor_point"
              v-for="icon in shareLinkArr"
              :key="icon.id"
              :name="icon.icon"
              :size="variables.link_icon_btn_size"
              @click="onShareLink(icon.sign)"
              v-zoom
            >
            </icon-font>
          </div>
        </div>
      </div>
    </div>
    <a id="downloadQRCODE"></a>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { callJava, isNativeClient } from '@/utils/helper'
import QRCode from 'qrcodejs2'
import copy from 'copy-to-clipboard'
import { userShareStatistics } from '@/api/modules/agent.js'
import { FACEBOOK_SHARE, WHATSAPP_SHARE, LINK_SHARE, TWITTER_SHARE, TG_SHARE } from '@/api/setting'
import variables from '@style/views/referGift.modules.scss'
export default {
  props: {
    //分享Url
    linkUrl: {
      type: String,
      default: ''
    },
    //分享数据
    linkData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      state: false,
      qrcode: null,
      variables
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp', 'systemConfig', 'user']),
    // 外链共享权限
    shareLinkArr() {
      // share_app = [1,2,3,4]
      const { share_app = [] } = this.systemConfig
      // 映射列表
      const shareLink = [
        {
          id: '1',
          icon: 'icon-icon_refer_whatsapp',
          sign: 'whatsapp'
        },
        {
          id: '2',
          icon: 'icon-icon_refer_facebook',
          sign: 'facebook'
        },
        {
          id: '3',
          icon: 'icon-icon_refer_line',
          sign: 'line'
        },
        {
          id: '4',
          icon: 'icon-icon_refer_zalo',
          sign: 'zalo'
        },
        {
          id: '5',
          icon: 'icon-icon_TG',
          sign: 'TG'
        },
        {
          id: '6',
          icon: 'icon-icon_twitter',
          sign: 'twitter'
        }
      ]
      return shareLink
        .map(item => {
          if (share_app.includes(item.id)) return item
          return null
        })
        .filter(f => f)
    }
  },
  methods: {
    /**
     * 打开动画
     * @param {*} money 金额
     * @param {*} cancel 关闭回调
     */
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        this.createQrcode()
      })
    },
    // 关闭动画
    hide() {
      this.state = false
      if (this.remove) this.remove()
    },
    // 生成分享二维码
    createQrcode() {
      if (!this.$refs['QRCrodeRef']) return
      // 有新的二维码地址了，先把之前的清除掉
      if (this.qrcode) this.$refs['QRCrodeRef'].innerHTML = ''

      this.qrcode = new QRCode(this.$refs['QRCrodeRef'], {
        id: 'qrCrodeId',
        text: this.linkUrl,
        width: 100,
        height: 100,
        correctLevel: QRCode.CorrectLevel.Q //纠错码 L:1,M:0,Q:3,H:2
      })
    },
    // 复制到剪贴板
    onCopy() {
      const isCopy = copy(this.linkUrl)
      if (isCopy) {
        this.$pop.message().show({ content: this.$t('UI_Copied') })
        // 上报打点统计
        userShareStatistics({ token: this.user.token })
      }
    },
    // 下载二维码
    downloadIamge() {
      const nodeList = Array.prototype.slice.call(this.qrcode?._el?.children || [])
      const canvas = nodeList[0]
      if (canvas && canvas.nodeName && canvas.nodeName.toLocaleLowerCase() == 'canvas') {
        const url = canvas.toDataURL('image/png')
        document.getElementById('downloadQRCODE').setAttribute('href', url)
        document.getElementById('downloadQRCODE').setAttribute('download', `${new Date().getTime()}.png`)
        document.getElementById('downloadQRCODE').click()

        this.$pop.message().show({ content: this.$t('UI_Download_Iamge') })
        if (isNativeClient()) callJava('saveQR', { str: url }, new Date().getTime())
      }
    },
    /**
     * 打开共享外链
     * @param {String} sign 标识
     */
    onShareLink(sign = '') {
      if (!sign) return

      const { share_content = '', link = '' } = this.linkData
      switch (sign) {
        case 'whatsapp':
          open(`${WHATSAPP_SHARE}?text=${share_content}    ${link}`)
          break
        case 'facebook':
          open(`${FACEBOOK_SHARE}?u=${link}&content=${share_content}`)
          break
        case 'line':
          if (isNativeClient()) {
            callJava('LineAppShareUrl', { url: share_content + '    ' + link })
          } else {
            open(`${LINK_SHARE}?text=${share_content}&url=${encodeURIComponent(link)}`)
          }
          break
        case 'zalo':
          if (isNativeClient()) callJava('zaloShareCallback', { msg: share_content, link })
          break
        case 'TG':
          open(`${TG_SHARE}?text=${encodeURIComponent(share_content)}&url=${encodeURIComponent(link)}`)
          break
        case 'twitter':
          open(`${TWITTER_SHARE}?text=${encodeURIComponent(share_content)}&url=${encodeURIComponent(link)}`)
          break
        default:
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/referGift.modules.scss';
.refer_link_box {
  width: 100%;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: $refer_link_bg;
  border-radius: $refer_link_border_radius;
  .link_top {
    width: $link_top_width;
    padding: $link_top_padding;
    @include setBox(row, space-between, center);
    .link_top_left {
      margin: $link_top_left_margin;
      font-weight: $link_top_left_font_weight;
      font-size: $link_top_left_font_size;
      color: $link_top_left_color;
    }
    .share_btn_icon {
      cursor: pointer;
    }
  }
  .link_content {
    position: relative;
    padding: $link_content_padding;
    &::before {
      content: '';
      position: absolute;
      bottom: $link_content_before_bottom;
      right: $link_content_before_right;
      width: $link_content_before_width;
      height: $link_content_before_height;
      background: $link_content_before_bg;
    }
    .link_content_Share {
      width: $link_content_Share_width;
      margin: $link_content_Share_margin;
      background: $link_content_Share_bg;
      border-radius: 8px;
      font-weight: $link_content_Share_border_radius;
      font-size: $link_content_Share_font_weight;
      color: $link_content_Share_color;
      @include setBox(row, space-between, center);
      .link_content_Share_text {
        flex: 1;
        height: $link_content_Share_text_height;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        padding: $link_content_Share_text_padding;
      }
      .link_content_Share_btn {
        font-size: $link_content_Share_btn_font_size;
        border-radius: $link_content_Share_btn_border_radius;
      }
    }
    .link_content_code {
      margin: $link_content_code_margin;
      @include setBox(row, center, center);
      width: $link_content_code_width;
      height: $link_content_code_height;
      background: $link_content_code_bg;
      overflow: hidden;
      border-radius: $link_content_code_border_radius;
    }
    .link_content_text {
      margin: $link_content_text_margin;
      font-weight: $link_content_text_font_weight;
      font-size: $link_content_text_font_size;
      color: $link_content_text_color;
      text-align: $link_content_text_text_align;
    }
    .link_btn {
      margin: $link_btn_margin;
    }
    .link_icon {
      padding: $link_icon_padding;
      .link_icon_title {
        font-weight: $link_icon_title_font_weight;
        font-size: $link_icon_title_font_size;
        color: $link_icon_title_color;
      }
      .link_icon_btn {
        @include setBox(row, flex-start, center);
        margin: $link_icon_btn_margin;
        gap: $link_icon_btn_gap;
      }
    }
  }
}

// app 端兼容样式
@media (max-width: 1080px) {
  .refer_link_box {
    width: $refer_link_width_app;
    position: fixed;
    top: $refer_link_top_app;
    left: $refer_link_left_app;
    transform: $refer_link_transform_app;
  }
}
// pc 端兼容样式
@media (min-width: 1080px) {
  .refer_link_box {
    width: $refer_link_width_pc;
    position: fixed;
    top: $refer_link_top_pc;
    left: $refer_link_left_pc;
    transform: $refer_link_transform_pc;
  }
}
</style>
